<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  methods: {
    getProducts() {
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/products`;
      vm.$http.get(api).then((response) => {
        console.log(response.data);
        if (response.data.success) {
          vm.$router.push({
            path: '/',
          });
        }
      });
    },
  },
  created() {
    // const vm = this;
    // vm.getProducts();
  },
};

</script>

<style lang="scss">
@import './assets/all';
</style>



