<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right">
      <button class="btn btn-primary mt-3" @click.prevent="openCouponModal(true)">
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td> {{ item.title }} </td>
          <td> {{ item.percent }} %</td>
          <td> {{item.due_date | formatDate }} </td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else class="text-muted"></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click.prevent="openCouponModal(false, item)"></button>
            <button class="btn btn-outline-danger btn-sm" @click.prevent="delCouponModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input type="text" class="form-control" id="title" placeholder=""
              v-model="tempCoupon.title">
            </div>
            <div class="form-group">
              <label for="coupon_code"></label>
              <input type="text" class="form-control" id="coupon_code" placeholder=""
              v-model="tempCoupon.code">
            </div>
            <div class="form-group">
              <label for="due_date"></label>
              <input type="date" class="form-control" id="due_date"
              v-model="tempCoupon.due_date">
            </div>
            <div class="form-group">
              <label for="price"></label>
              <input type="number" class="form-control" id="price" placeholder=""
              v-model="tempCoupon.percent">
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox" id="is_enabled"
                v-model="tempCoupon.is_enabled" :true-value="1" :false-value="0">
                <label class="form-check-label" for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary" @click.prevent="updateCoupon(tempCoupon.id)">
              <span v-if="isNew"></span>
              <span v-else></span>
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click.prevent="removeCoupon"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  data() {
    return {
      coupons: [],
      isNew: false,
      isLoading: false,
      tempCoupon: {
        title: '',
        is_enabled: false,
        percent: 100,
        due_date: 0,
        code: '',
      },
    };
  },
  methods: {
    getCoupons() {
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/coupons`;
      vm.isLoading = true;
      vm.$http.get(api).then((response) => {
        vm.isLoading = false;
        // console.log(response.data);
        vm.coupons = response.data.coupons;
      });
    },
    openCouponModal(isNew, item) {
      const vm = this;
      if (isNew) {
        vm.tempCoupon = {};
        vm.isNew = true;
      } else {
        vm.tempCoupon = Object.assign({}, item);
        vm.isNew = false;
      }
      $('#couponModal').modal('show');
    },
    delCouponModal(item) {
      const vm = this;
      vm.tempCoupon = Object.assign({}, item);
      $('#delCouponModal').modal('show');
    },
    updateCoupon(id) {
      const vm = this;
      let api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/coupon`;
      let httpMethod = 'post';
      if (!vm.isNew) {
        api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/coupon/${id}`;
        httpMethod = 'put';
      }

      vm.$http[httpMethod](api, { data: vm.tempCoupon }).then((response) => {
        if (response.data.success) {
          $('#couponModal').modal('hide');
          vm.getCoupons();
          if (vm.isNew) {
            console.log('Copon ');
          } else {
            console.log('Copon ');
          }
        } else {
          $('#couponModal').modal('hide');
          vm.getCoupons();
          if (vm.isNew) {
            console.log('Copon ');
          } else {
            console.log('Copon ');
          }
        }
      });
    },
    removeCoupon() {
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.CUSTOM_PATH}/admin/coupon/${vm.tempCoupon.id}`;
      vm.isLoading = true;
      vm.$http.delete(api).then((response) => {
        vm.isLoading = false;
        // console.log(response.data);
        if (response.data.success) {
          $('#delCouponModal').modal('hide');
          vm.getCoupons();
          console.log('');
        } else {
          $('#delCouponModal').modal('hide');
          vm.getCoupons();
          console.log('');
        }
      });
    },
  },
  filters: {
    formatDate(n) {
      return n.split('-').join('/');
    },
  },
  created() {
    const vm = this;
    vm.getCoupons();
  },
};
</script>



