import Vue from 'vue';
import Router from 'vue-router';
// import HelloWorld from '@/components/HelloWorld';
import Dashboard from '@/components/Dashboard';
import Login from '@/components/pages/Login';
import Products from '@/components/pages/Products';
import Orders from '@/components/pages/Orders';
import Coupons from '@/components/pages/Coupons';
import CustomOrders from '@/components/pages/CustomOrders';
import CustomCheckout from '@/components/pages/CustomCheckout';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      // meta: { requiresAuth: true },
      children: [
        {
          path: 'products',
          name: 'Products',
          component: Products,
          meta: { requiresAuth: true },
        },
        {
          path: 'orders',
          name: 'Orders',
          component: Orders,
          meta: { requiresAuth: true },
        },
        {
          path: 'coupons',
          name: 'Coupons',
          component: Coupons,
          meta: { requiresAuth: true },
        },
      ],
    },
    {
      path: '/',
      name: 'Dashboard2',
      component: Dashboard,
      // meta: { requiresAuth: true },
      children: [
        {
          path: 'custom-orders',
          name: 'CustomOrders',
          component: CustomOrders,
        },
        {
          path: 'custom-checkout/:orderId',
          name: 'CustomCheckout',
          component: CustomCheckout,
        },
      ],
    },
    {
      path: '*',
      redirect: 'login',
    },
  ],
});



